.btn {
  display: inline-block;
  overflow: visible;
  margin: 0;
  border: 1px solid #b1b1bc;
  border-radius: 3px;
  padding: 0 1em;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.1);
  font: inherit;
  line-height: calc(2em - 2px);
  -webkit-font-smoothing: antialiased;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  white-space: nowrap;
  background-color: #e4e4ea;
}

.btn,
.btn:link,
.btn:visited,
.btn:hover,
.btn:focus,
.btn:active {
  color: #3b3b3f;
}

.btn:hover,
.btn:focus {
  border-color: #9999a3;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.15);
  background-color: #b1b1bc;
}

.btn:active {
  border-color: #7c7c84;
  box-shadow: none;
  background-color: #9999a3;
}

button.btn {
  -webkit-appearance: button;
}

/**
* Establish default space between adjacent buttons
*/
.btn + .btn {
  margin-left: 0.6666666667rem;
}

/**
   * Buttons with no styles (good a11y practice to use <button> for actions rather than <a>).
   */
.btn--bare {
  border: 0;
  border-radius: 0;
  padding: 0;
  line-height: inherit;
}

.btn--bare,
.btn--bare:link,
.btn--bare:visited,
.btn--bare:hover,
.btn--bare:active,
.btn--bare:focus {
  box-shadow: none;
  background: none;
  color: inherit;
}

/**
   * Change button colors for primary buttons
   */
.btn--primary {
  border-color: #2b5ea2;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.15);
  background-color: #3e87e8;
}

.btn--primary,
.btn--primary:link,
.btn--primary:visited,
.btn--primary:hover,
.btn--primary:focus,
.btn--primary:active {
  color: white;
}

.btn--primary:hover,
.btn--primary:focus {
  border-color: transparent;
  background-color: #3573c5;
}

.btn--primary:active {
  border-color: transparent;
  background-color: #2b5ea2;
}

/**
   * Change button colors for tertiary buttons
   */
.btn--tertiary {
  border-color: #b1b1bc;
  box-shadow: none;
  background-color: white;
}

.btn--tertiary,
.btn--tertiary:link,
.btn--tertiary:visited,
.btn--tertiary:hover,
.btn--tertiary:focus,
.btn--tertiary:active {
  color: #3e87e8;
}

.btn--tertiary:hover,
.btn--tertiary:focus {
  border-color: #9999a3;
  box-shadow: none;
  background-color: #e4e4ea;
}

.btn--tertiary:active {
  border-color: #7c7c84;
  box-shadow: none;
  background-color: #b1b1bc;
}

/**
   * Change button colors for disabled buttons
   */
.btn--disabled,
.btn--disabled:link,
.btn--disabled:visited,
.btn--disabled:hover,
.btn--disabled:active,
.btn--disabled:focus,
.btn[disabled],
.btn[disabled]:link,
.btn[disabled]:visited,
.btn[disabled]:hover,
.btn[disabled]:active,
.btn[disabled]:focus {
  border-color: #d1d0d8;
  box-shadow: none;
  cursor: not-allowed;
  background-color: white;
  color: #d1d0d8;
}

/**
   * Small buttons.
   */
.btn--small {
  font-size: 0.8rem;
  line-height: calc(1.6666666667em - 2px);
}

/**
   * Large buttons.
   */
.btn--large {
  font-size: 1.3333333333rem;
}

/**
   * Call-to-action buttons.
   */
.btn--huge {
  font-size: 2rem;
}

/**
   * Full-width buttons.
   */
.btn--full {
  width: 100%;
}

/**
   * Round-ended buttons.
   *
   * 1. Overly-large value to ensure the radius rounds the whole end of the
   *    button.
   */
.btn--icon {
  width: 2em;
  border-radius: 6.6666666667em;
  padding: 1px 0 0 1px;
}

