}

.u-fluid,
.u-full,
.u-full-width {
  width: 100%;
}

.u-float.right {
  float: right !important;
}

.u-float.left {
  float: left !important;
}

img.u-full-width {
  width: 100%;
  max-width: 100%;
}

img.responsive {
  height: auto;
  width: 100%;
  max-width: 100%;
}

img.round {
  border-radius: 50%;
}

.u-no-border {
  border: 0 !important;
}

.t-d-u,
.text-decoration--underline {
  text-decoration: underline;
}

.t-d-s,
.text-decoration--strike {
  text-decoration: line-through;
}

.text-decoration--none {
  text-decoration: none;
}

.u-color--primary,
a.u-color--primary {
  color: #007acc !important;
}

.u-color--alt,
a.u-color--alt {
  color: #b6c3cc !important;
}

.u-color--dark,
a.u-color--dark {
  color: #748b99 !important;
}

.u-color--light,
a.u-color--light {
  color: #e2e8ed !important;
}

.u-color--green,
a.u-color--green {
  color: #a4c497 !important;
}

.u-color--red,
a.u-color--red {
  color: #e99ba3 !important;
}

.u-color--blue,
a.u-color--blue {
  color: #adb9ff;
}

.u-color--yellow,
a.u-color--yellow {
  color: #e8e082;
}

.u-color--white,
a.u-color--white {
  color: white !important;
}

.u-hover-btn-outline:hover {
  background: transparent;
  border: 1px solid;
}

.u-hover-btn-outline-reverse:hover {
  background: auto;
  border: 1px solid transparent;
}

.u-hover-opacity-0:hover {
  opacity: 0;
}

.u-hover-opacity-25:hover {
  opacity: 0.25;
}

.u-hover-opacity-50:hover {
  opacity: 0.5;
}

.u-hover-opacity-75:hover {
  opacity: 0.75;
}

.u-hover-opacity-100:hover {
  opacity: 1;
}

.u-hover-scale-0:hover {
  transform: scale(0);
}

.u-hover-scale-25:hover {
  transform: scale(0.25);
}

.u-hover-scale-50:hover {
  transform: scale(0.5);
}

.u-hover-scale-100:hover {
  transform: scale(1);
}

.u-hover-scale-105:hover {
  transform: scale(1.05);
}

.u-hover-scale-125:hover {
  transform: scale(1.25);
}

/* ARIA + Screen reader assists */
.sr-only {
  height: 1px;
  width: 1px;
  position: absolute;
  overflow: hidden;
  margin: -1px;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

[aria-hidden="true"] {
  display: none;
}

